.header {
  position: fixed;
  top: 0;
  z-index: 10;
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 80px;
  color: white;
  background-color: #4cb5b5;
}

.header .search-box input {
  width: 350px;
  height: 30px;
  padding: 0 1rem;
  font-size: 1.2rem;
  border: none;
  border-radius: 5px;
  outline: none;
}
